@charset "UTF-8";
.title_bar {
  display: block;
  margin: 1rem 0;
  font-size: 1.6rem;
  font-weight: bold;
}
@media (max-width: 576px) {
  .title_bar {
    font-size: 1.2rem;
  }
}

.bar {
  background-color: #95202b;
  width: 15px;
  height: 3px;
}

.title_text {
  font-weight: bold;
  font-size: 6rem !important;
  margin: 1rem 0;
  display: block;
}
@media (max-width: 992px) {
  .title_text {
    font-size: 3rem !important;
    text-align: center;
  }
}
@media (max-width: 576px) {
  .title_text {
    font-size: 2rem !important;
  }
}

.title_red {
  color: #95202b;
  font-size: 2rem;
  margin: 1rem 0;
}
@media (max-width: 576px) {
  .title_red {
    font-size: 1.6rem !important;
  }
}

.btn, .btn_trans {
  border: none;
  padding: 10px 20px;
  border-radius: 15px 5px 15px 5px;
  color: #f6f6f6;
  margin: 10px 0;
  cursor: pointer;
  overflow: hidden;
  transition: all 0.5s ease-in-out;
}
.btn:hover, .btn_trans:hover {
  background-color: #95202b;
  border-color: #f6f6f6;
}

.btn_trans {
  background-color: transparent;
  border: 3px solid #95202b;
  transition: all 0.5s ease cubic-bezier(0.075, 0.82, 0.165, 1);
}

.btn-info {
  border: 1px solid var(#95202b) !important;
  background-color: #1b1813;
}

.btn_arrow {
  display: flex;
  justify-content: center;
  align-items: center;
  color: #95202b;
  font-size: 1.6rem;
  cursor: pointer;
  position: relative;
  font-weight: bold;
}
.btn_arrow:hover {
  color: #95202b;
}
.btn_arrow:hover span {
  animation: arrow 1s ease infinite;
}
.btn_arrow span {
  position: relative;
  font-size: 2rem;
  padding: 0 0.2rem;
}
@media (max-width: 992px) {
  .btn_arrow {
    font-size: 1.2rem !important;
    padding: 0 0.2rem;
  }
}

.first_letter {
  color: #95202b;
}

@keyframes arrow {
  0% {
    left: 0;
  }
  100% {
    left: 20%;
    opacity: 0;
  }
}
.eror_theme {
  color: #95202b;
  font-size: 0.8rem;
}

@media (max-width: 576px) {
  .typping {
    font-size: 1rem;
  }
}

.page-transition {
  transform-origin: top right;
  transform: perspective(600px) rotateY(-180deg);
  transition: transform 0.5s ease-out, opacity 0.5s ease-out;
  opacity: 0;
}

.page-transition.ng-enter {
  transform: perspective(600px) rotateY(0);
  opacity: 1;
}

.page-transition.ng-leave {
  transform: perspective(600px) rotateY(-180deg);
  opacity: 0;
}

.page-transition {
  position: relative;
}

.router-outlet {
  position: absolute;
  width: 100%;
  height: 100%;
  top: 0;
  left: 0;
}

@keyframes typing-animation {
  from {
    width: 0;
  }
  to {
    width: 100%;
  }
}
.start-typing, .delay {
  overflow: hidden;
  white-space: break-spaces;
  animation: typing-animation 1.5s steps(400, end) forwards;
}

.typing-container {
  /* Définissez une largeur fixe pour le conteneur */
}

.op {
  opacity: 0;
}

.delay {
  animation-delay: 1.5s;
}

#bodymovin {
  width: 100%;
  height: 100%;
  display: block;
  overflow: hidden;
  transform: translate3d(0, 0, 0);
  text-align: center;
  opacity: 1;
}

#close {
  transition: opacity 0.4s ease 0.1s, color 0.2s, transform 0.2s;
  opacity: 0;
  border: none;
  background: none;
  position: fixed;
  top: 1em;
  right: 1em;
  font-size: 2em;
  padding: 1em;
  cursor: pointer;
  z-index: 1;
}

.open #close {
  opacity: 1;
}

#close:focus {
  outline: none;
  transform: scale(1.1);
}

.modal {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
}
.modal__content {
  opacity: 0;
  transform: scale(0.8) translate3D(0, -20px, 0);
  transition: opacity 0.2s, transform 0.8s cubic-bezier(0.09, 0.52, 0.25, 1);
  text-align: center;
  display: flex;
  align-items: center;
  flex-direction: column;
  padding: 1em;
}
.modal h1 {
  text-transform: uppercase;
  letter-spacing: 0.4em;
}
.modal p {
  max-width: 25em;
  line-height: 1.9;
}

.open .modal__content {
  opacity: 1;
  transform: scale(1) translate3D(0, 0, 0);
}

* {
  padding: 0;
  margin: 0;
  box-sizing: border-box;
  text-decoration: none;
}

