.imgCard {
  background-position: bottom center !important;
  background-repeat: no-repeat !important;
  background-size: cover !important;
  display: flex;
  justify-content: center;
  align-items: center;
  border-radius: 5px 5px 0 0;
  max-width: 100%;
  height: 100%;
  width: 60px;
  min-height: inherit;
  overflow: hidden;
  position: absolute;
  border-top-left-radius: 80px;
  border-bottom-left-radius: 80px;
  border-left: 10px solid #dddddd;
  z-index: 10;
  transition: width 0.6s, opacity 0.9s;
  right: 0;
}
.imgCard a {
  opacity: 0;
}
.imgCard:hover {
  width: 100%;
  border-top-left-radius: 10px;
  border-bottom-left-radius: 10px;
}
.imgCard:hover a {
  opacity: 1;
}

.space {
  width: 150px;
}

.shadow_card {
  box-shadow: 0 0 10px 1px rgba(0, 0, 0, 0.305);
}